import React from 'react';
import styled from 'styled-components';
import { StaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';

import { Section, Container } from '@components/global';


const TEAM = [
  {
    name: 'Ansin Lau',
    image: 'ansin.jpg',
    role: 'Co-founder, designer',
    intro: 'I am an enthusiastic designer and curious social observer with a passion to make societal changes with design. I hold two bachelor degrees in Design and Sociology from Taipei National University. During my studies in design at Aalto University in 2017, I started to seriously work on the idea that could solve the music listening problem disturbing many of the hard of hearing people like myself.',
  },
  {
    name: 'Kevin Cheng',
    image: 'kevin.jpg',
    role: 'Co-founder, engineer',
    intro: 'I am immersed in the world of digital fabrication with my Physics major and Fab Diploma FabAcademy in 2016. Knowing how to make almost anything gives me power to turn any idea from zero to one. In the information age, we as  subjects are more and more transformed into digital form, transforming our consciousness. My dream is to extend human abilities to explore the physical world in the digital age.',
  },
];

const Team = () => (
  <StaticQuery
    query={graphql`
      query {
        allFile(filter: { sourceInstanceName: { eq: "team" } }) {
          edges {
            node {
              relativePath
              childImageSharp {
                fluid(maxWidth: 400, maxHeight: 400) {
                  ...GatsbyImageSharpFluid
                }
              }
            }
          }
        }
      }
    `}
    render={data => (
      <Section id="team" accent="secondary">
        <Container style={{ position: 'relative' }}>
          <h2>The Team</h2>
          <TeamGrid>
            {TEAM.map(({ name, image, role, intro }) => {
              const img = data.allFile.edges.find(
                ({ node }) => node.relativePath === image
              ).node;

              return (
                <div>
                  <Img fluid={img.childImageSharp.fluid} alt={name} />
                  <Title>{name}</Title>
                  <Subtitle>{role}</Subtitle>
                  <IntroText>{intro}</IntroText>
                </div>
              );
            })}
          </TeamGrid>
        </Container>
      </Section>
    )}
  />
);

const TeamGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fill, 300px);
  grid-template-rows: min-content;
  grid-gap: 50px;
  justify-content: space-between;
  width: 60%;
  margin-top: 72px;

  @media (max-width: ${props => props.theme.screen.lg}) {
    justify-content: start;
  }

  @media (max-width: ${props => props.theme.screen.md}) {
    width: 100%;
    grid-template-columns: repeat(auto-fill, minmax(160px, 1fr));
  }

  @media (max-width: ${props => props.theme.screen.xs}) {
    grid-gap: 24px;
  }
`;

const Art = styled.figure`
  width: 800px;
  margin: -80px 0;
  position: absolute;
  top: 0;
  left: 70%;

  @media (max-width: ${props => props.theme.screen.lg}) {
    top: 20%;
  }

  @media (max-width: ${props => props.theme.screen.md}) {
    display: none;
  }
`;

const ArtMobile = styled.figure`
  width: 100%;
  margin: 0;
  display: none;
  margin-top: 64px;
  margin-bottom: -60%;

  @media (max-width: ${props => props.theme.screen.md}) {
    display: block;
  }
`;

const Title = styled.p`
  margin-top: 16px;
  color: ${props => props.theme.color.black.regular};
`;

const Subtitle = styled.p`
  ${props => props.theme.font_size.regular};
  color: ${props => props.theme.color.grayscale.S800};
`;

const IntroText = styled.p`
  ${props => props.theme.font_size.small};
  color: ${props => props.theme.color.grayscale.S700};
`;

export default Team;
